* {
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;
    background-color:#f2babd;
    color: white;
    transition: "0.1s";
}
.kvYdOK {
    margin: auto auto -3px;
    padding: 64px 0px 0px;
    text-align: center;
}
.kvYdOK h1 {
    font-size: 44px;
    max-width: 600px;
    font-weight: 400;
    font-family: "Helvetica Neue LT W05_75 Bold", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Open Sans", sans-serif;
    margin: 0px auto 40px;
    padding: 0px 60px;
}
.gJuuYA {
    padding: 14px 32px;
    background:#ffc9cc none repeat scroll 0% 0%;
    border-radius: 0px;
    font-size: 16px;
    font-family: "Helvetica Neue LT W05 65 Medium", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Open Sans", sans-serif;
    margin-bottom: 80px;
    display: inline-block;
    transition: background 0.25s ease-in-out 0s;
}
a {
    color: inherit;
    text-decoration: none;
}
inline @only screen and (max-width: 800px){
.kvYdOK img {
    width: 500px;
}
}
inline @only screen and (max-width: 960px){
.kvYdOK img {
    width: 560px;
}
}
.kvYdOK img {
    height: auto;
    width: 640px;
}
